<script setup>
import { ref } from 'vue'
import { onLoad } from '@dcloudio/uni-app'

// 打开百度
const openBaidu = () => {
	const url = 'https://www.baidu.com'
	const title = '百度搜索'
	uni.navigateTo({
		url: `/pages/base/pages/webview?url=${encodeURIComponent(url)}&title=${encodeURIComponent(title)}`
	})
}

// 打开微信公众号
const openWechat = () => {
	const url = 'https://mp.weixin.qq.com'
	const title = '微信公众平台'
	uni.navigateTo({
		url: `/pages/base/pages/webview?url=${encodeURIComponent(url)}&title=${encodeURIComponent(title)}`
	})
}

// 打开GitHub
const openGithub = () => {
	const url = 'https://github.com'
	const title = 'GitHub'
	uni.navigateTo({
		url: `/pages/base/pages/webview?url=${encodeURIComponent(url)}&title=${encodeURIComponent(title)}`
	})
}

onLoad(() => {
	console.log('WebView示例页面加载')
})
</script>

